// src/components/navigation/dynamic-breadcrumb.jsx
"use client"

/**
 * DynamicBreadcrumb
 * -----------------------------------------------------------------------------
 * -  URL  "/"   " >  > "  breadcrumb  .
 * -     <BreadcrumbPage> ,
 *      <BreadcrumbLink>   .
 *
 *   
 * - includeHome:       ( true)
 * - homeLabel, homeHref:  / 
 * - hide:      (: ["settings"])
 * - overrides:   
 *    1) : { "eqp": "", "logs": "" }
 *    2) : (segment, index, segments) => string | ReactNode
 *
 *  :
 * <DynamicBreadcrumb
 *   overrides={{ "line-dashboard": " " }}
 *   hide={["app"]}  // /app/line-dashboard  " " 
 * />
 */

import Link from "next/link"
import { Fragment } from "react"
import { usePathname } from "next/navigation"

import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
  BreadcrumbLink,
} from "@/components/ui/breadcrumb"

/* -----------------------------------------------------------------------------
 * :   +    
 * -------------------------------------------------------------------------- */

/** decodeURIComponent       try/catch   */
function safeDecodeURIComponent(value) {
  try {
    return decodeURIComponent(value)
  } catch {
    return value
  }
}

/** "my-page-id"  "My Page Id"     */
function toTitleCase(segment) {
  return safeDecodeURIComponent(String(segment ?? ""))
    .replace(/-/g, " ")
    .replace(/\b\w/g, (ch) => ch.toUpperCase())
}

/* -----------------------------------------------------------------------------
 * : pathname  breadcrumb  
 * -------------------------------------------------------------------------- */

/**
 * segment  
 * - overrides   
 * - overrides    
 * -     toTitleCase
 */
function resolveLabel(segment, index, segments, overrides) {
  if (typeof overrides === "function") {
    return overrides(segment, index, segments) ?? toTitleCase(segment)
  }
  if (overrides && typeof overrides === "object" && segment in overrides) {
    return overrides[segment]
  }
  return toTitleCase(segment)
}

/**
 * pathname "/"  [{ href, segment, label, isLast }, ...]  
 */
function buildBreadcrumbs(pathname, { overrides, hide, includeHome, homeLabel, homeHref }) {
  if (!pathname) return []

  // "/a/b/c"  ["a","b","c"]
  const segments = pathname.split("/").filter(Boolean)

  //      Set
  const hideSet = new Set(Array.isArray(hide) ? hide : [])

  const items = []

  // :  
  if (includeHome) {
    items.push({
      href: homeHref,
      segment: "__home__",
      label: homeLabel,
      isLast: segments.length === 0, // ("/")    
      isHome: true,
    })
  }

  //    breadcrumb 
  segments.forEach((segment, index) => {
    if (hideSet.has(segment)) return

    const href = `/${segments.slice(0, index + 1).join("/")}`
    const label = resolveLabel(segment, index, segments, overrides)
    const isLast = index === segments.length - 1

    items.push({ href, segment, label, isLast, isHome: false })
  })

  return items
}

/* -----------------------------------------------------------------------------
 * 
 * -------------------------------------------------------------------------- */

export function DynamicBreadcrumb({
  overrides = {},              //   (  )
  hide = [],                   //  
  includeHome = true,          //   
  homeLabel = "Home",          //  
  homeHref = "/",              //  
}) {
  const pathname = usePathname()

  //     memo     .
  const crumbs = buildBreadcrumbs(pathname, {
    overrides,
    hide,
    includeHome,
    homeLabel,
    homeHref,
  })

  //  includeHome=false   
  if (crumbs.length === 0) return null

  return (
    <Breadcrumb>
      <BreadcrumbList>
        {crumbs.map((crumb, index) => (
          <Fragment key={`${crumb.href}-${index}`}>
            {/*      X */}
            {index > 0 && <BreadcrumbSeparator />}

            <BreadcrumbItem>
              {crumb.isLast ? (
                // :  
                <BreadcrumbPage>{crumb.label}</BreadcrumbPage>
              ) : (
                //     
                <BreadcrumbLink asChild>
                  <Link href={crumb.href}>{crumb.label}</Link>
                </BreadcrumbLink>
              )}
            </BreadcrumbItem>
          </Fragment>
        ))}
      </BreadcrumbList>
    </Breadcrumb>
  )
}
