// src/features/navigation/components/app-sidebar.jsx
"use client"

import React from "react"
import { useRouter } from "next/navigation"
import { toast } from "sonner"

import { NAVIGATION_CONFIG } from "../config/navigation-config"
import { NavProjects } from "./nav-projects"
import { NavUser } from "./nav-user"
import { TeamSwitcher } from "./team-switcher"
import { ActiveLineProvider } from "./active-line-context"
import { NavMain } from "./nav-main"
import { useAuth } from "@/features/auth"
import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarHeader,
  SidebarRail,
} from "@/components/ui/sidebar"

export function AppSidebar({ lineOptions, ...props }) {
  const router = useRouter()
  const { user, logout } = useAuth()
  const { navMain, projects } = NAVIGATION_CONFIG
  //      
  const teams = Array.isArray(lineOptions) ? lineOptions : []

  const handleLogout = React.useCallback(async () => {
    try {
      await logout()
      toast.success(" .")
      router.push("/login")
    } catch (error) {
      console.error(error)
      toast.error(" .    .")
    }
  }, [logout, router])

  return (
    <ActiveLineProvider lineOptions={teams}>
      <Sidebar collapsible="icon" {...props}>
        <SidebarHeader>
          <TeamSwitcher lines={teams} />
        </SidebarHeader>
        <SidebarContent>
          <NavMain items={navMain} />
          <NavProjects projects={projects} />
        </SidebarContent>
        <SidebarFooter>
          <NavUser user={user} onLogout={handleLogout} />
        </SidebarFooter>
        <SidebarRail />
      </Sidebar>
    </ActiveLineProvider>
  )
}
